.video-responsive {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
}

button {
    padding: 0;
    border: none;
    background: none;
    /*width: 100%;*/
}

.list-button {
    padding: 0;
    border: none;
    background: none;
    width: 100%;
}

.video-responsive video {
    left: 0;
    top: 0;
    padding: 1vh;
    height: 100%;
    width: 100%;
    position: absolute;
}

.title {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 100;
    line-height: 1.5;
}

.selectTypeProject {
    z-index: 1;
    position: fixed;
    width: 31vh;
    height: 5vh;
    left: 9.6vh;
    top: 9.6vh;
    border-radius: 1vh;
    padding: 0.1vh;
    font-size: 1.5rem;
    font-weight: 100;
    line-height: 1.2;
}

a {
    color: #2f2f2f;
}

a:hover {
    color: #2f2f2f;
    text-decoration: none;
}
@media screen and (max-width: 812px) {
    .title {
        text-align: center;
        font-size: 1.5rem;
        font-weight: 5;
        line-height: 1.2;
    }

    .selectTypeProject {
        position: fixed;
        width: 42.8vh;
        height: 5vh;
        left: 3.6vh;
        top: 9.6vh;
        border-radius: 1vh;
        padding: 0.1vh;
        font-size: 1.2rem;
        font-weight: 80;
        line-height: 1.1;
    }

}

