.grid {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 1vh;
    grid-auto-rows: minmax(1vh, auto);
}

.grid div:nth-child(1) {
    grid-column: 1;
    grid-row: 3;
}

.grid div:nth-child(2) {
    grid-column: 2;
    grid-row: 3;
}

.title {
    color: black;
    text-align: left;
    font-size: 1.6rem;
    font-weight: 1.5;
    line-height: 1.2;
}
.text {
    color: black;
    text-align: left;
    font-size: 1.2rem;
    font-weight: 1;
    line-height: 1.2;
}

@media screen and (max-width: 812px) {
    body {
        overflow: visible;
    }
    .grid {
        display: grid;
        grid-template-rows: repeat(1,1fr);
        grid-gap: 1vh;
    }

    .grid div:nth-child(1) {
        left: 5.23vh;
        top: 15.6vh;
        grid-column: 1;
        grid-row: 1;
    }

    .grid div:nth-child(2) {
        left: 5.23vh;
        top: 17.6vh;
        grid-column: 1;
        grid-row: 2;
    }
}
